@import url(https://fonts.googleapis.com/css?family=Francois+One|Varela+Round);
#root{
  height:100vh;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}




ul {
  list-style: none;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

a{
  text-decoration: none;
  color:black;
}

body{
font-family: 'Varela Round', sans-serif;
}

.App{
  height: 100%;
  background-color:#f9f9f9;
}

.home-container{
    height: 90%;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
}

.button-style{
    border: 1px solid;
    background: violet;
    box-sizing: unset;
    padding: 5px;
    font-family: inherit;
    margin: 0px 10px;
}

.clock-container{
    align-self: self-end;
}

.links{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 15px 0px;
}

.links p {
  margin: 5px 0px;
}

.links a{
    text-decoration: underline;
}




.clock-container{
    width: 240px;
    height: 220px;

}

.seconds-container{
    width:100%;
    height: 25px;
}

.minutes-container{
    width:100%;
    height: 25px;
}

.hours-container{
    width:100%;
    height: 25px;
}
.day-container{
    width:100%;
    height: 25px;
}

.month-container{
    width:100%;
    height: 25px;
}

.year-container{
    width:100%;
    height: 25px;
}
.close-button{
    color:red;
}

.stuff-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

@media only screen and (max-width: 690px) {
    .stuff-container{
        grid-template-columns: 1fr 1fr;
    }
  }
@media only screen and (max-width: 460px) {
    .stuff-container{
        grid-template-columns: 1fr;
    }
  }

.card-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.card-footer p {
    padding: 10px;
}



.nav{
    margin: 0;
    display: flex;
    align-items: center;
    font-family: 'Francois One', sans-serif;
    height: 10%;
}

.nav > .logo{

    font-size: 2rem;
    width: 10%;
    text-align: center;
    padding: 1rem;
}
.nav > .nav-links{
  width: 90%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.nav > .nav-links > .nav-item{
    padding: 1rem;
}

.nav-container > ul > li > a{
text-decoration: none;
color:black;
}

.stuff{
    color:#63A186;
}


